import QRCode from 'qrcode';

/**
 * generateQRCode
 *
 * @param {string} text
 * @param {dom} location
 * @param {string} defaultLevel
 *
 * @return {string} url
 */
export default function generateQRCode(text, location = null, defaultLevel = 'H') {
  QRCode.toDataURL(text, { errorCorrectionLevel: defaultLevel }, (error, url) => {
    if (error) {
      return false;
    }

    if (location) {
      location.setAttribute('src', url);
    }

    return url;
  });
}



